import * as React from "react";

import {srcsetGenerator, sizesGenerator} from '../../images/srcsetGenerator';

const Image = (props) => {
    let srcSet = '';
    if (props && props.srcSet) {
        srcSet = srcsetGenerator(props.srcSet);
    }

    const sizes = sizesGenerator(props.srcSet);
    return (
        <img 
            {...props} 
            srcSet={srcSet ? srcSet : null} 
            sizes={sizes ? sizes : null} 
            alt={props.alt} 
        />
    );
};

export default Image;
