import React from "react";
import renderAst from "./renderAst";
import PageFooter from "./components/Footer";
import AffiliateInfo from "./components/AffiliateInfo";
import BlogTitleAndCover from "./components/BlogTitleAndCover";
import GooglePhotosContext from "./Providers/googlePhotoContext";

import "./styles.scss";

export const MainSectionWrapper = (props) => {
  const {showTitle} = props;

  return (
    <>
      <main className="MainSection">
        <div className="MainSectionInner">
          {showTitle &&
            <BlogTitleAndCover {...props} />
          }

          {props.children}
        </div>
      </main>

      {props.affiliate &&
        <AffiliateInfo />
      }

      <PageFooter />
    </>
  );
}

const Stream = (props) => {
  if (!props.data.page) {
    return (
      <>
        <main className="MainSection"></main>
        <PageFooter />
      </>
    );
  }
  const { childMarkdownRemark } = props.data.page;
  const { htmlAst } = childMarkdownRemark;

  return (
    <GooglePhotosContext.Provider value={props.data.allGooglePhotosPhoto?.nodes}>
      <MainSectionWrapper {...props.data.page}>
        {renderAst(htmlAst)}
      </MainSectionWrapper>
    </GooglePhotosContext.Provider>
  );
}

export default Stream 