import * as React from "react";

import {srcsetGenerator, sizesGenerator} from '../../images/srcsetGenerator';

const Source = (props) => {
    if (!props) {
        return <source {...props}></source>
    }

    let {srcSet} = props;
    if (props.type === "image/png" || props.type === "image/webp") {
        if (srcSet) {
            srcSet = srcsetGenerator(srcSet);
        }
    }

    const sizes = sizesGenerator(srcSet);
    return <source {...props} srcSet={srcSet ? srcSet : srcSet} sizes={srcSet ? sizes : null}></source>
};

export default Source;
