import React, { Suspense } from "react";
import rehypeReact from "rehype-react";

import RegistryItem from "./components/RegistryItem";
import ContentSection from "./components/ContentSection";
import Link from "./components/Link";
import { H1, H2, H3, H4, H5, H6 } from "./components/Header";
import HorizontalBox from "./components/HorizontalBox";
import VerticalBox from "./components/VerticalBox";
import Separator from "./components/Separator";
import {QuarterColumn, HalfColumn, OneThirdColumn} from "./components/Column";
import Invitation from "./components/Invitation";
import Margin from "./components/Margin";
import FAQBullet from "./components/FAQBullet";
import CenteredText from "./components/CenteredText";
import Stack from "./components/Stack";
import Image from "./components/Image";
import Source from "./components/Source";
import {UnsortedListContainer, OrderedListContainer} from "./components/ListContainer";
import {PhotoCaption, PhotoCaptionContainer} from "./components/PhotoCaption";

import "./renderAst.scss";

const GooglePhotosAlbum = React.lazy(() => import('./components/GooglePhotosAlbum'));
const Codeblock = React.lazy(() => import('./components/Codeblock'));
const PhotoBoard = React.lazy(() => import('./components/PhotoBoard'));
const Carousel = React.lazy(() => import('./components/Carousel'));
const Countdown = React.lazy(() => import('./components/Countdown'));
const StripeProduct = React.lazy(() => import('./components/StripeProduct'));
const RSVPButton = React.lazy(() => import('./components/RSVPButton'));
const WeddingPartyItem = React.lazy(() => import('./components/WeddingPartyItem'));


const SuspenseLoader = (className = '') => {
  return (
    <div className={className}>Loading...</div>
  );
};

function LazyGooglePhotosAlbum(props) {
  return (
    <Suspense fallback={<SuspenseLoader />}>
      <GooglePhotosAlbum {...props} />
    </Suspense>
  );
}

function LazyCodeblock(props) {
  return (
    <Suspense fallback={<SuspenseLoader />}>
      <Codeblock {...props} />
    </Suspense>
  );
}

function LazyPhotoBoard(props) {
  return (
    <Suspense fallback={<SuspenseLoader />}>
      <PhotoBoard {...props} />
    </Suspense>
  );
}

function LazyCarousel(props) {
  return (
    <Suspense fallback={<SuspenseLoader />}>
      <Carousel {...props} />
    </Suspense>
  );
}

function LazyCountdown(props) {
  return (
    <Suspense fallback={<SuspenseLoader className="SuspenseCountdown" />}>
      <Countdown {...props} />
    </Suspense>
  );
}

function LazyStripeProduct(props) {
  return (
    <Suspense fallback={<SuspenseLoader />}>
      <StripeProduct {...props} />
    </Suspense>
  );
}

function LazyRSVPButton(props) {
  return (
    <Suspense fallback={<SuspenseLoader className="SuspenseRSVPButton" />}>
      <RSVPButton {...props} />
    </Suspense>
  );
}

function LazyWeddingPartyItem(props) {
  return (
    <Suspense fallback={<SuspenseLoader className="SuspenseWeddingPartyItem" />}>
      <WeddingPartyItem {...props} />
    </Suspense>
  );
}

const renderAst = new rehypeReact({
  createElement: React.createElement,
  components: { 
    "carousel": LazyCarousel,
    "codeblock": LazyCodeblock,
    "registryitem": RegistryItem,
    "photoboard": LazyPhotoBoard,
    "halfcolumn": HalfColumn,
    "quartercolumn": QuarterColumn,
    "contentsection": ContentSection,
    "countdown": LazyCountdown,
    "horizontalbox": HorizontalBox,
    "verticalbox": VerticalBox,
    "separator": Separator,
    "weddingpartyitem": LazyWeddingPartyItem,
    "onethirdcolumn": OneThirdColumn,
    "invitation": Invitation,
    "margin": Margin,
    "faqbullet": FAQBullet,
    "stripeproduct": LazyStripeProduct,
    "photocaption": PhotoCaption,
    "photocaptioncontainer": PhotoCaptionContainer,
    "centeredtext": CenteredText,
    "stack": Stack,
    "rsvpbutton": LazyRSVPButton,
    "img": Image,
    "source": Source,
    "googlephotosalbum": LazyGooglePhotosAlbum,
    "a": Link,
    "ul": UnsortedListContainer,
    "ol": OrderedListContainer,
    "h1": H1,
    "h2": H2,
    "h3": H3,
    "h4": H4,
    "h5": H5,
    "h6": H6,
   }
}).Compiler;

export default renderAst;