import React from "react";

import {H1} from "../Header";

import {GatsbyImage, getImage} from "gatsby-plugin-image";
import moment from "moment/moment";

import "./styles.scss";

const BlogTitleAndCover = (props) => {
    const {cover, name, modifiedTime} = props;

    const blogCreatedTime = modifiedTime ? moment(modifiedTime).format('MMM DD, YYYY') : '';
    return (
        <div className="BlogTitleAndCover">
            <H1>{name}</H1>
            {blogCreatedTime &&
                <div className="BlogPublishedDate">
                    Published: {blogCreatedTime}
                </div>
            }
            {cover?.image &&
                <div className="BlogTitleAndCoverImage">
                    <GatsbyImage 
                        image={getImage(cover.image)} 
                        alt={cover.alt}
                    />
                </div>
            }
        </div>
    );
}

export default BlogTitleAndCover;