const MAX_WIDTH = 786;

export const srcsetGenerator = (srcSet) => {
    if (!srcSet) {
        return '';
    }

    const splitSrcSet = srcSet.split(" ");
    const out = [];
    for (let i = 0; i < splitSrcSet.length; i+= 2) {
        const width = splitSrcSet[i+1];
        const curWidth = parseInt(width.slice(0, width.length - 1));
        if (curWidth > MAX_WIDTH) {
            continue;
        }

        out.push(`${splitSrcSet[i]} ${splitSrcSet[i + 1]}`)
    }
    srcSet = out.join(', ');

    return srcSet;
};

